.encyclopedie__main {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: 3fr 1fr;
  position: relative;
  overflow: hidden;
}
.encyclopedie__right-menu {
  width: 100%;
  height: 100%;
  background-image: url("../../assets/guide/bg_navigation.jpg");
  padding: 2rem 2rem 2rem 3rem;
  position: absolute;
  right: -100vw;
  overflow: auto;
  transition: 0.3s;
}
@media (min-width: 950px) {
  .encyclopedie__right-menu {
    position: relative;
    right: unset;
    border-left: 4px solid #db2748;
  }
}
.encyclopedie__right-menu.active {
  right: 0;
  border-left: 0;
  transition: 0.3s;
}
@media (min-width: 950px) {
  .encyclopedie__right-menu.active {
    position: relative;
    right: unset;
  }
}
.encyclopedie__right-menu h2 {
  font-size: 36px;
  text-transform: uppercase;
  color: #db2748;
  font-family: "Poppins", sans-serif;
  position: relative;
  margin-bottom: 1.5rem;
  line-height: 45px;
  z-index: 5;
  padding-left: 1rem;
}
.encyclopedie__right-menu ul {
  padding-left: 0;
  position: relative;
  z-index: 5;
  font-family: "Poppins", sans-serif;
}
.encyclopedie__right-menu ul li {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 600;
  max-height: 34px;
  overflow: hidden;
  transition: 0.6s;
  position: relative;
  padding: 0 0 0 8px;
}
.encyclopedie__right-menu ul li::before {
  content: "";
  position: absolute;
  top: 12px;
  left: 5px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #db2748;
  opacity: 0;
  transition-property: opacity, transform;
  transition-duration: 0.3s;
  transform: translateX(-5px);
}
.encyclopedie__right-menu ul li.active {
  max-height: 400px;
  transition: 0.6s;
}
.encyclopedie__right-menu ul li.active::before {
  opacity: 1;
  transform: translateX(0);
}
.encyclopedie__right-menu ul li.menu-buttons-container {
  cursor: pointer;
}
.encyclopedie__right-menu ul li .menu-buttons {
  color: white !important;
  display: block;
  padding: 0 0.75rem;
  width: fit-content;
  transition: 0.3s;
  height: 32px;
  line-height: 32px;
  background: transparent;
  box-shadow: none;
  transition-duration: 0.3s;
  transition-property: opacity, transform;
}
.encyclopedie__right-menu ul li:hover::before {
  opacity: 1;
  transform: translateX(0);
}
.encyclopedie__right-menu ul li.active::before {
  opacity: 1;
  transform: translateX(0);
}
.encyclopedie__right-menu ul li.menu-buttons-separator {
  height: 2px;
  margin: 10px 0 10px 20px;
  width: 80%;
  background-color: #db2748;
}
.encyclopedie__right-menu ul li.menu-buttons-separator::before {
  display: none;
}
.encyclopedie__right-menu .back-link {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  display: flex;
  align-items: center;
  gap: 10px;
  flex-shrink: 0;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 600;
  text-decoration: none;
}
.encyclopedie__right-menu .back-link::before {
  content: "";
  width: 100%;
  border-bottom: 1px solid white;
  position: absolute;
  bottom: -5px;
  left: 0;
  transform: scaleX(0);
  transform-origin: right center;
  transition: transform 0.3s;
}
.encyclopedie__right-menu .back-link:hover::before {
  transform: scaleX(1);
  transform-origin: left center;
}
